    content: "";
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: #ea947e;
    z-index: 1;
    position: absolute;
    -webkit-animation: pulse 1s ease 4, fadeOut 1s ease 4s forwards;
            animation: pulse 1s ease 4, fadeOut 1s ease 4s forwards;
}
@-webkit-keyframes pulse {
    from{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    to{
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
}
@keyframes pulse {
    from{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    to{
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
}

