.radio {
    position: absolute;
    visibility: hidden;
}

.radioLabel {
    margin-left: 30px;
}

.check {
    display: block;
    position: absolute;
    border: 2px solid #dbdada;
    border-radius: 100%;
    height: 18px;
    width: 18px;
    z-index: 5;
  }

.check::before {
    transition: all .2s ease-in-out;
    display: block;
    position: absolute;
    content: '';
    border-radius: 100%;
    height: 12px;
    width: 12px;
    top: 3px;
    left: 3px;
    margin: auto;
  }

.radio:checked ~ .check {
    border: 2px solid #ea947e;
}

.radio:checked ~ .check::before{
    background: #ea947e;
}
